import evcTabbar from '@/components/evcTabbar.vue'
import UniLoadMore from '@/components/uni-load-more.vue'
export default {
	components: {
				UniLoadMore,
				evcTabbar	
			},
	data() {
		return {
			swiperImg: [{
					Img: "../../../static/images/pagesA/login/banner.png"
				},
				{
					Img: "../../../static/images/pagesA/login/banner.png"
				},
				{
					Img: "../../../static/images/pagesA/login/banner.png"
				}
			],
			fontColor1:'#0099FF',
			indexImgSelect:'../../../static/images/evctabbar/indexselect.png',
			current: 0,
			swiperCurrent: 0,
			productList: [],
			activityId: '',
			productList2: [],
			curPage: 1,
			loadingType: 'more',
			total1:0,
			total2:0,
			levelBgc:''
		};
	},
	onLoad() {
		//此页做下拉刷新跟上拉加载
		var _self = this
		if(!uni.getStorageSync("token")){
			this.$base1._isLogin()
		}
		_self.getProduct()
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		// console.log(e.scrollTop)
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.curPage = 1;
		this.getProduct('refresh')
		
	},
	//上拉加载更多
	onReachBottom() {
		this.curPage++;
		this.getProduct('add');
	},

	onNavigationBarButtonTap() {
		console.log("22222")
	},

	methods: {
		//加载奖品列表 ，带下拉刷新和上滑加载
		getProduct(type = 'add', loading) {
			//没有更多直接返回
			if (type === 'add') {
				if (this.loadingType === 'nomore') {
					return;
				}
				this.loadingType = 'loading';
				
			} else {
				this.loadingType = 'more'
			}
			if (type === 'refresh') {
				
				this.productList2 = [];
			}
			//产品列表
			uni.request({
				url: this.baseUrl + "/product-list",
				data: {
					page: 1,
					count: 100000
				},
				header:{
					Authorization:uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if(res.data.status==1){
						this.productList = res.data.data.List
						
					}else{
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
					
				}
			})
			//我的广告包
			uni.request({
				url: this.baseUrl + "/my-product",
				data: {
					page: this.curPage,
					count: 1
				},
				header:{
					Authorization:uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					if (res.data.status == 1) {
						this.productList2 = this.productList2.concat(res.data.data.List)
						this.total2 = res.data.data.Total
						if (this.productList2.length == 0) {
							this.loadingType = '';
						} else if (this.productList2.length >=this.total2 ) {
							this.loadingType = 'nomore';
						} else {
							this.loadingType = 'more';
						}
						if (type === 'refresh') {
							if (loading == 1) {
								uni.hideLoading()
							} else {
								uni.stopPullDownRefresh();
							}
						}
					} else {
						uni.stopPullDownRefresh();
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
				}
			})
			
		},
		changeSwiper(e) {
			this.swiperCurrent = e.detail.current;
		},
		change() {
			if (this.active < this.list1.length - 1) {
				this.active += 1
			} else {
				this.active = 0
			}
		},
		showLevelBgc(level){
			if(level==1){
				return  'linear-gradient(#FF727C, #FFA8AE)'
			}else if(level==2){
				return  'linear-gradient(#7FCCFF, #0099FF)'
			}else if(level==3){
				return  'linear-gradient(#FFC744, #FF9100)'
			}
		},
		jumpToProductDetail(index){
			console.log(this.productList[index].Id)
			//携带参数跳转到产品详情页
			uni.navigateTo({
				url:"./product-detail?id="+this.productList[index].Id
			})
		},
		seeAll(){
			uni.navigateTo({
				url:"./profit"
			})
		},
		myAdDetail(index){
			uni.navigateTo({
				//携带参数跳转到详情页
				url:"./detail?id="+this.productList2[index].Id
			})
		}

	}

}
